@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;500&family=Texturina:wght@100;800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  margin-top: 0;
  background-color: rgba(134, 212, 186, 0.705);
}
/* 
h3 {
  text-align: center;
} */

table {
  width: 100%;
  background-color: rgba(204, 204, 189, 0.897);
}

.container {
  width: 88%;
}
.navbar {
  margin: 0;
  width: 100%;
  padding: 5px;
  justify-content: center;
  font-family: 'Texturina', serif;
  font-weight: 800;
  font-size: 25px;
}
.searchRow {
  width: 100%;
}

.searchBar {
  margin-bottom: 10px;
  justify-content: center;
}

.searchInput {
  margin-right: 15px;
}

.tableHeader {
  font-family: 'Quicksand', sans-serif;
  font-weight: 500;
}
